<template>
    <div class="wrap">
        <div class="tableCon">
            <el-row>
                <el-col :span="24">
                    <div class="grid-content bg-purple-dark typeMain">
                        <p style="padding-bottom: 20px">测量数据</p>
                            <el-table
                            :data="tableData"
                            style="width: 100%;border: 1px solid #eee;border-bottom: none;">
                            <el-table-column
                                prop="index"
                                label="编号"
                                width="280">
                            </el-table-column>
                            <el-table-column
                                prop="name"
                                label="参数"
                                width="280">
                            </el-table-column>
                            <el-table-column
                                prop="list[0].num"
                                label="实测参数"
                                width="280">
                            </el-table-column>
                            <el-table-column
                                prop="mileage.num"
                                label="里程"
                                width="280">
                            </el-table-column>
                            <el-table-column
                                prop="list[0].sitename"
                                label="统计分析">
                            </el-table-column>
                        </el-table>
                    </div>

                </el-col>
            </el-row>
        </div>
    </div>
</template>
<script>
import api from "@/api/index.js";
export default {
    data() {
        return {
            currentPage: 1, //初始页
            pagesize:10,    //    每页的数据
            count: 0,
            tableData: [],
            page: 0,
        }
    },
    methods: {
        // 获取小车数据
        getSmartCarData() {
            api.getSmartCarData().then(res => {
                console.log(res.data.msg);
                res.data.msg.forEach((item, index) => {
                    item.index = index;
                })
                this.tableData = res.data.msg;
            })
        },
    },
    created() {
        this.getSmartCarData();
        setInterval(() => {
            this.getSmartCarData();
        }, 5000)
    },
}
</script>

<style lang="stylus" scoped>
.wrap 
    border-radius 10px
    overflow hidden
    padding 20px
    .Header
        color #fff
        .select-item
            display flex
            align-items center
            color #fff
            .select_
                color #fff
                border 1px solid #fff
                padding 4px 10px
                border-radius 4px
                cursor pointer
    .tableCon
        background #ffffff
        margin-top 20px
        padding 20px
        border-radius 12px
        overflow hidden
</style>



